import Vue from 'vue'

import VueRouter from 'vue-router'
import Dashboard from '../views/Dashboard.vue'
import IssueBook from '../views/IssueBook.vue'
import CollectBook from '../views/CollectBook.vue'
import ManageMember from '../views/ManageMember.vue'
import ManageBook from '../views/ManageBook.vue'
import ManagePublisher from '../views/ManagePublisher.vue'
import ManageAuthor from '../views/ManageAuthor.vue'
import UserDetails from '../views/UserDetails.vue'
import BookDetails from '../views/BookDetails.vue'
import History from '../views/History.vue'
import AddMember from '../views/AddMember.vue'
import AddBook from '../views/AddBook.vue'
import LoginUser from '../views/LoginUser.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'login',
        component: LoginUser
    },
    {
        path: '/Dashboard',
        name: 'dashboard',
        component: Dashboard
    },
    {
        path: '/Issue_Book',
        name: 'issueBook',
        component: IssueBook
    },
    {
        path: '/Collect_Book',
        name: 'collectBook',
        component: CollectBook
    },
    {
        path: '/Manage_Member',
        name: 'manageMember',
        component: ManageMember
    },
    {
        path: '/Manage_Book',
        name: 'manageBook',
        component: ManageBook
    },
    {
        path: '/Manage_Publisher',
        name: 'managePublisher',
        component: ManagePublisher
    },
    {
        path: '/Manage_Author',
        name: 'manageAuthor',
        component: ManageAuthor
    },
    {
        path: '/User_Details',
        name: 'userDetails',
        component: UserDetails
    },
    {
        path: '/Book_Details',
        name: 'bookDetails',
        component: BookDetails
    },
    {
        path: '/History',
        name: 'history',
        component: History
    },
    {
        path: '/Add_Member',
        name: 'addMember',
        component: AddMember
    },
    {
        path: '/Add_Book',
        name: 'addBook',
        component: AddBook
    },
    {
        path: '*',
        redirect: '/'
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})
export default router