<template>
  <div class="buttons">
    <router-link :to="goToLink" class="button is-info">View {{who}} info</router-link>
    <router-link :to="addLink" class="button is-info">Add {{who}} info</router-link>
    <button class="button is-danger" @click="confirmDelete=true">Delete {{who}}</button>
    <DeleteModal :open="confirmDelete" @close="confirmDelete=false" />
  </div>
</template>

<script>
import DeleteModal from "@/components/DeleteModal.vue";
export default {
  name: "ManagementActions",
  props: ["who","goToLink","addLink"],
  components: {
    DeleteModal
  },
  data() {
    return { confirmDelete: false };
  }
};
</script>