<template>
  <div class="modal DeleteModal" v-bind:class="{'is-active':open}">
    <div class="modal-background"></div>
    <div class="modal-content">
      <div class="box">
        <h1 class="title is-4">Record(s) will be removed.</h1>
        <h2 class="subtitle is-4">Are you sure want to Delete ?</h2>
        <div class="buttons">
          <button class="button is-danger" @click="closeModal">No, Keep it</button>
          <button class="button is-success" @click="closeModal">Yes, I'm sure</button>
        </div>
      </div>
    </div>
    <button class="modal-close is-large" aria-label="close" @click="closeModal"></button>
  </div>
</template>

<script>
export default {
  name: "DeleteModal",
  props: ["open"],
  methods:{
    closeModal:function(){
      this.$emit('close')
    }
  }
};
</script>