<template>
  <nav class="breadcrumb" aria-label="breadcrumbs">
    <ul>
      <li v-for="(nLink, index) in links" v-bind:key="index">
        <router-link :to="nLink.link">{{nLink.show}}</router-link>
      </li>
    </ul>
  </nav>
</template>
<script>
import { mapState } from "vuex";
export default {
  props: ["bLink", "bShow"],
  computed: {
    ...mapState(["links"])
  },
  mounted: function() {
      var nLink = {
        show: this.bShow,
        link: this.bLink
      };
      this.$store.dispatch("createLink", nLink);
    }
};
</script>