<template>
  <div class="level-right InfoHandler">
    <span
      title="Click to change the view mode"
      @click="changeMode"
      class="level-item button is-rounded"
      :class="{'has-text-info':edit,'has-text-primary':read}"
    >
      <span class="icon">
        <span :class="{'is-hidden':read}">
          <i class="fas fa-lock"></i>
        </span>
        <span :class="{'is-hidden':edit}">
          <i class="fas fa-lock-open"></i>
        </span>
      </span>
      <span>{{viewMode}}</span>
    </span>
  </div>
</template>
<style scoped>
.InfoHandler span {
  cursor: pointer;
}
</style>
<script>
export default {
  name: "InfoHandler",
  data() {
    return {
      viewMode: "Read Only",
      read: false,
      edit: true
    };
  },
  methods: {
    changeMode: function() {
      if (this.viewMode == "Edit") {
        this.viewMode = "Read Only";
        this.read = false;
        this.edit = true;
      } else {
        this.viewMode = "Edit";
        this.read = true;
        this.edit = false;
      }
    }
  }
};
</script>