import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        links: [],
        isAuthenticated: false
    },
    mutations: {
        ADD_LINK(state, link) {
            if (state.links.length == 5) {
                state.links.splice(0, 1);
            }
            if (state.links.filter(l => l.show == link.show).length == 0) {
                state.links.push(link);
            }
        },
        AUTHENTICATE(state, isAuth) {
            state.isAuthenticated = isAuth;
        }
    },
    actions: {
        createLink({ commit }, link) {
            commit('ADD_LINK', link);
        },
        loginUser({ commit }, isAuth) {
            commit('AUTHENTICATE', isAuth);
        }
    },
    modules: {}
})