<template>
  <div class="modal LogoutModal" v-bind:class="{'is-active':open}">
    <div class="modal-background"></div>
    <div class="modal-content">
      <div class="box">
        <h1 class="title is-4">We will miss you.</h1>
        <h2 class="subtitle is-4">Are you sure want to Logout ?</h2>
        <div class="buttons">
          <button class="button is-danger" @click="closeModal">No, Go back</button>
          <button class="button is-success" @click="logout();closeModal();">Yes, I'm sure</button>
        </div>
      </div>
    </div>
    <button class="modal-close is-large" aria-label="close" @click="closeModal"></button>
  </div>
</template>

<script>
export default {
  name: "LogoutModal",
  props: ["open"],
  methods:{
    closeModal:function(){
      this.$emit('close')
    },
    logout:function(){
      this.$store.dispatch('loginUser',false);
      this.$router.push({ name: "login" });
    }
  }
};
</script>