      <div class="column is-4 is-offset-4">
        <h3 class="title has-text-black">Login</h3>
        <hr class="login-hr" />
        <p class="subtitle has-text-black">Please login to proceed.</p>
        <div class="box">
          <figure class="avatar">
            <img class="has-background-primary" width="128" height="128" src="../assets/boy.png" />
          </figure>
          <form>
            <div class="field">
              <div class="control">
                <input
                  v-model="usr"
                  class="input is-large"
                  type="email"
                  placeholder="Your Email"
                  autofocus
                />
              </div>
            </div>

            <div class="field">
              <div class="control">
                <input
                  v-model="pwd"
                  class="input is-large"
                  type="password"
                  placeholder="Your Password"
                />
              </div>
            </div>
            <div class="field">
              <label class="checkbox">
                <input type="checkbox" />
                Remember me
              </label>
            </div>
            <p v-on:click="login" class="button is-block is-info is-large is-fullwidth">
              Login
              <i class="fas fa-sign-in-alt" aria-hidden="true"></i>
            </p>
          </form>
        </div>
        <p class="has-text-grey">
          <a href="../">Sign Up</a> &nbsp;·&nbsp;
          <a href="../">Forgot Password</a> &nbsp;·&nbsp;
          <a href="../">Need Help?</a>
        </p>
      </div>
    </div>
  </section>
</template>

