<script>
export default {
  name: "LoginUser",
  data() {
    return { usr: "mukun", pwd: "edith" };
  },
  methods: {
    login: function() {
      if (this.usr == "mukun" && this.pwd == "edith")
        this.$store.dispatch("loginUser", true);
      else this.$store.dispatch("loginUser", false);
      if (this.$store.state.isAuthenticated)
        this.$router.push({ name: "dashboard" });
    }
  }
};
</script>